.demo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2.5rem 0rem; }
  .demo__range-slider-container:nth-of-type(1) {
    width: 70%;
    display: grid;
    grid-template-columns: 70% auto;
    grid-template-rows: 31.25rem max-content;
    justify-content: center;
    align-items: center;
    padding: 3.125rem 0rem;
    border-bottom: 0.0625rem solid #555555; }
  .demo__range-slider-container:nth-of-type(2) {
    width: 70%;
    display: grid;
    grid-template-columns: 40% auto;
    grid-template-rows: 43.75rem max-content;
    justify-content: center;
    align-items: center;
    padding: 3.125rem 0rem;
    border-bottom: 0.0625rem solid #555555; }
    .demo__range-slider-container:nth-of-type(2) .range-slider__handle {
      opacity: 1;
      background: linear-gradient(180deg, #6fcf97 0%, #66d2ea 100%);
      border-radius: 50%; }
    .demo__range-slider-container:nth-of-type(2) .range-slider__background-handle {
      opacity: 1;
      background: white;
      border-radius: 50%; }
    .demo__range-slider-container:nth-of-type(2) .range-slider__empty-strip {
      background: white;
      border: 0.125rem solid rgba(31, 32, 65, 0.25);
      box-sizing: border-box;
      border-radius: 0.375rem; }
    .demo__range-slider-container:nth-of-type(2) .range-slider__filled-strip {
      background: linear-gradient(180deg, #6fcf97 0%, #66d2ea 100%);
      border-radius: 0.375rem; }
  .demo__color-customizer {
    width: 70%;
    display: flex;
    padding: 3.125rem 0rem;
    border-bottom: 0.0625rem solid #555555; }
  .demo__slider-with-physic {
    width: 70%;
    height: 25rem;
    display: flex;
    justify-content: center;
    padding: 3.125rem 0rem;
    border-bottom: 0.0625rem solid #555555; }
