.color-customizer {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  padding: 0rem 12.5rem; }
  .color-customizer__sliders {
    width: 50%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-self: flex-end; }
  .color-customizer__red {
    width: 3.125rem;
    height: 25rem; }
    .color-customizer__red .range-slider__empty-strip {
      background: linear-gradient(to left, red, white);
      border: 0.0625rem solid #5a5a5a;
      border-radius: 0.125rem; }
  .color-customizer__green {
    width: 3.125rem;
    height: 25rem; }
    .color-customizer__green .range-slider__empty-strip {
      background: linear-gradient(to left, lime, white);
      border: 0.0625rem solid #5a5a5a;
      border-radius: 0.125rem; }
  .color-customizer__blue {
    width: 3.125rem;
    height: 25rem; }
    .color-customizer__blue .range-slider__empty-strip {
      background: linear-gradient(to left, blue, white);
      border: 0.0625rem solid #5a5a5a;
      border-radius: 0.125rem; }
  .color-customizer__alpha {
    width: 3.125rem;
    height: 25rem; }
    .color-customizer__alpha .range-slider__empty-strip {
      background: linear-gradient(to left, black, white);
      border: 0.0625rem solid #5a5a5a;
      border-radius: 0.125rem; }
  .color-customizer__color-square {
    width: 18.75rem;
    height: 18.75rem; }
  .color-customizer .range-slider {
    border: 0rem; }
  .color-customizer .range-slider__additional-container {
    display: none; }
  .color-customizer .range-slider__handle {
    border-radius: 50%;
    opacity: 1;
    background: linear-gradient(to left, #505050, #adadad);
    box-shadow: inset 0.125rem 0rem 0.125rem rgba(0, 0, 0, 0.384); }
  .color-customizer .range-slider__background-handle {
    border-radius: 50%;
    opacity: 1;
    background: linear-gradient(to left, #acacac, #e9e9e9);
    box-shadow: inset 0.125rem 0rem 0.125rem rgba(0, 0, 0, 0.384); }
  .color-customizer .range-slider__tooltip {
    border-radius: 0.3125rem;
    background: linear-gradient(180deg, #cecece, #868686);
    box-shadow: inset 0.125rem 0rem 0.3125rem rgba(0, 0, 0, 0.384); }
