.options {
  width: 100%;
  display: grid;
  grid-column-start: 1;
  grid-column-end: 3;
  padding: 0.625rem;
  border: 0.125rem solid #3a25b3; }
  .options__label {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 0.3125rem; }
  .options__checkboxes-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    grid-column-start: 1;
    grid-column-end: 3;
    align-items: baseline;
    margin-bottom: 0.3125rem; }
  .options__input {
    width: 30%;
    height: 1.5625rem;
    border: 0.0625rem solid black;
    font-size: 1.25rem; }
  .options__checkbox-input {
    width: 1.25rem;
    height: 1.25rem;
    margin-right: 0.3125rem; }
  .options__text {
    display: inline-flex;
    align-items: flex-end;
    margin: 0rem;
    margin-left: 0.625rem;
    text-align: center;
    font-size: 1.25rem; }
  .options__title {
    margin-bottom: 0.3125rem;
    font-size: 1.25rem; }
  .options__inputs {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-bottom: 0.3125rem; }
